<template>
    <el-row class="sharelistBox">
        <div v-if="this.$route.name === 'Share' && !this.$route.query.keywords" class="shareTitle">
<!--            <div class="ui label">-->
<!--                <a :href="'/Share?classId=' + classId">{{className}}</a>-->
<!--            </div>-->
<!--            <ul v-if="sonclassList" class="shareclassTwo">-->
<!--                <li v-for="(citem, index) in sonclassList">-->
<!--                    <a :href="'#Share?classId='+classId+'&classtowId'+citem.class_id" :class="citem.class_id==classtowId?'active':''">-->
<!--                        {{citem.cate_name}}-->
<!--                    </a>-->
<!--                </li>-->
<!--            </ul>-->
        </div>

        <el-col :span="24" class="s-item tcommonBox" v-for="(item, index) in articleList" :key="'article'+index">
<!--            <span class="s-round-date">-->
<!--                <span class="month" v-html="showInitDate(item.create_time, 'month')+'月'"></span>-->
<!--                <span class="day" v-html="showInitDate(item.create_time, 'date')"></span>-->
<!--            </span>-->
            <header>
                <h1>
                    <a :href="'/DetailShare?aid='+item.id" target="_blank">
                        {{item.title}}
                    </a>
                </h1>
                <h2>
                    <i class="el-icon-user myicon"></i>发表于
                    <i class="el-icon-date myicon"></i>
                    <span v-html="showInitDate(item.create_time, 'all')">
                        {{showInitDate(item.create_time, 'all')}}
                    </span>
                    <i class="el-icon-chat-line-round myicon"></i>{{item.comment}}条
                    <i class="el-icon-postcard myicon"></i>
                    <a :href="'/Share?classId='+item.class_id">{{item.cate_name}}</a>
                </h2>
            </header>
            <div class="article-content">
                <p style="text-indent: 2em;">
                    {{item.description}}
                </p>
                <p style="max-height: 300px; overflow: hidden; text-align: center;">
                    <img :src="item.image" alt="img" class="maxW">
                </p>
            </div>
            <div class="viewdetail">
                <a class="tcolors-bg" :href="'/DetailShare?aid='+item.id" target="_blank">
                    阅读全文
                </a>
            </div>
        </el-col>
        <el-col class="viewmore">
            <a href="javasript:void(0);" class="tcolors-bg" v-show="hasMore" @click="addMoreFun">点击加载</a>
            <a href="javasript:void(0);" class="tcolors-bg" v-show="!hasMore">无更多数据</a>
        </el-col>
    </el-row>
</template>

<script>
    import {ArtClassData, ShowArticleAll, initTimeDate} from '../utils/server'
    export default {
        name: "articleList",
        data(){
            return {
                hasMore: false,
                articleList: [{
                    id: 0,
                    title: '测试',
                    create_time: '2014-04-23 18:55:49:123',
                    comment: 0,
                    cate_name: 'hello world',
                    description: '肯定是卡卡反馈会发出阿八福晋能否阿克苏发卡机从北京',
                    image: 'static/img/headbg05'
                },{
                    id: 1,
                    title: '测试',
                    create_time: '2014-04-23 18:55:49:123',
                    comment: 'test',
                    cate_name: 'hello world',
                    description: '肯定是卡卡反馈会发出阿八福晋能否阿克苏发卡机从北京',
                    image: 'static/img/headbg05'
                }],
                artId: 0,
                classId: 0,
                keywords: '',
                level: 0,
                sendId: 0,
                className: '',
                shareClass: [
                    {classId:1,name:'技术分享'},
                    {classId:2,name:'闲言碎语'},
                    {classId:3,name:'事件簿'},
                    {classId:4,name:'创作集'}
                ]
            }
        },
        methods:{
            showInitDate (oldDate, full) {
                console.log(oldDate, new Date(oldDate));
                return initTimeDate(oldDate, full);
            },
            getArticleList (initpage) {
                this.keywords = this.$store.state.keywords;
                this.classId = (this.$route.query.classId === undefined ? 0 : parseInt(this.$route.query.classId));
                this.sendId = this.classId;
                this.level = this.keywords ? 0 : 1;

                ArtClassData(function (msg) {
                    this.shareClass = msg;
                })

                //显示当前的分类名称
                for(let i=0; i<this.shareClass.length; i++){
                    if(this.classId === this.shareClass[i].class_id){
                        this.className = this.shareClass[i].name;
                    }
                }

                //初始化， 文章id以0开始
                this.artId = initpage ? 0 : this.artId;
                ShowArticleAll(this.artId, this.sendId, this.keywords, this.level, (result)=>{
                    if(result.code == 1001){
                        let msg = result.data;
                        if(msg.length>0 && msg.length<10){
                            this.hasMore = false;
                        }
                        else{
                            this.hasMore = true;
                        }
                        this.articleList = initpage ? msg : this.articleList.concat(msg);
                        this.artId = msg[msg.length-1].id;
                    }else{
                         this.hasMore = false;
                         this.articleList = initpage ? [] : this.articleList;
                    }
                })
            },
            addMoreFun() {
                this.getArticleList(false);
            },
            routeChange() {
                this.getArticleList(true);
            }
        },
        watch: {
            '$route': 'routeChange',
            '$store.state.keywords': 'routeChange'
        },
        created(){
            this.routeChange();
        }
    }
</script>

<style scoped>
    /*分享标题*/
    .shareTitle{
        margin-bottom: 40px;
        position: relative;
        border-radius: 5px;
        background: #fff;
        padding:15px;
    }
    .shareclassTwo{
        width:100%;
    }
    .shareclassTwo li{
        display: inline-block;
    }
    .shareclassTwo li a{
        display: inline-block;
        padding:3px 7px;
        margin:5px 10px;
        color:#fff;
        border-radius: 4px;
        background: #64609E;
        border: 1px solid #64609E;
        transition: transform 0.2s linear;
        -webkit-transition: transform 0.2s linear;
    }
    .shareclassTwo li a:hover{
        transform: translate(0,-3px);
        -webkit-transform: translate(0,-3px);
    }
    .shareclassTwo li a.active{
        background: #fff;
        color:#64609E;

    }
    /*文章列表*/
    .sharelistBox{
        transition: all 0.5s ease-out;
        font-size: 15px;
        margin-top: 20px;
    }
    .viewdetail a {
        background: #555555;
    }
    .viewmore a{
        background: #555555;
    }
    .myicon{
        margin-left: 10px;
        margin-right: 2px;
    }

</style>